export default {
  layout: "Dum-content",
  mounted() {
    this.getevent();
  },
  data() {
    return {
      events: [],
      event: {
        title: "",
        description: "",
        new_image: "",
        slug: "",
      },
      errors: {},
      base_url: process.env.url,
    };
  },
  methods: {
    getevent() {
      this.$axios
        .$get("/event/show")
        .then((res) => {
          this.events = res;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    eventEdit(id) {
      $("#eventUpdate").modal("show");
      this.$axios
        .$get("/event/edit/" + id)
        .then((res) => {
          this.event = res;

        })
        .catch((err) => {
          console.log(err);
        });

    },
    eventUpdate() {
      let formData = new FormData();
      formData.append('title', this.event.title)
      formData.append('description', this.event.description)
      if (this.event.new_image) {
        formData.append('image', this.event.new_image)
      }

      this.$axios
        .$post("/event/update/" + this.event.id, formData)
        .then((res) => {
          this.getevent();
          $("#eventUpdate").modal("hide");
          this.$toaster.success(res.message);
          this.errors = "";
        })
        .catch((err) => {
          console.log(err);
          this.errors = err.response.data.errors;
        });
    },
    deleteevent(id) {
      if (confirm("Are you sure to delete this event?")) {
        this.$axios
          .$get("/event/delete/" + id)
          .then((res) => {
            this.getevent();
            this.$toaster.error(res.message);
          })
          .catch((err) => {
            console.log(err);
          });
      }

    },
    eventStatus(id) {
      this.$axios
        .$get("/event/status/" + id)
        .then((res) => {
          this.getevent();
          this.$toaster.success(res.message);
        })
        .catch((err) => {
          console.log(err);
        });
    },









  }
};
